
















































import Vue from 'vue';
import RomaUnit from '../../components/RomaUnit.vue';
import RomaPurpose from '../../components/RomaPurpose.vue';
import RomaOa from '../../components/RomaOa.vue';
import RomaActivityList from '../../components/RomaActivityList.vue';

export default Vue.extend({
    name: 'OrganizerUnit',
    beforeRouteUpdate (to, from, next) {
      // console.log(next);
      next();
    },
    props: {
        unit: {
            type: Object,
            default () {
                return this.$store.state.organizer.selectedSchool.selectedUnit
            }
        }
    },
    data() {
        return {
            acitvedTab: null,
            tabs: [
                {
                    label: 'Propsito de la unidad',
                    icon: 'far fa-edit',
                    component: RomaPurpose,
                },
                {
                    label: 'Objetivos en la unidad',
                    icon: 'fas fa-bullseye',
                    component: RomaOa,
                },
                {
                    label: 'Actividades en la unidad',
                    icon: 'fas fa-user-check',
                    component: RomaActivityList,
                },
            ],
        };
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            if (vm.$store.state.organizer.selectedSchool && vm.$store.state.organizer.selectedSchool.selectedUnit) {
                return true;
            }

            next({name: 'organizer'});
        });

    },
    components: {
        RomaUnit,
        RomaOa,
    }
});
